<template>
  <div>
    <el-dialog :close-on-click-modal="false" :show-close="false" width="40%" title="" :visible.sync="this.$store.state.dialogFormVisible">
      <el-form :rules="rules" ref="dataForm" label-position="left" :model="form">
        <el-form-item label="" prop="type">
          <el-radio v-model="form.type" :label="1"> {{blogInfo.webSite.author}} </el-radio>
          <el-radio v-model="form.type" :label="2"> {{blogInfo.webSite.author}} </el-radio>
        </el-form-item>
        <el-form-item label="" prop="email" :label-width="formLabelWidth">
          <el-input placeholder="" v-model="form.email" />
        </el-form-item>
        <el-form-item label="" prop="title" :label-width="formLabelWidth">
          <el-input placeholder="" v-model="form.title" />
        </el-form-item>
        <div class="contentInput">
          <el-form-item label="" prop="content" :label-width="formLabelWidth">
            <el-input placeholder="" :rows="6" type="textarea" v-model="form.content" :label-width="formLabelWidth" />
            <a @click="screenshot" v-if="!form.imgUrl" style="color: #03a9f4"><i class="el-icon-camera"></i> </a>
            <img v-else :src="form.imgUrl">
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
        <el-button type="primary" @click="submit"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { addFeedback } from "../api";
export default {
  metaInfo: {
    meta: [
      {
        name: "keyWords",
        content: ",www.chales.online,,,", //
      },
      {
        name: "description",
        content:
          ",,,",
      },
    ],
  },
  data: function () {
    return {
      formLabelWidth: "80px",
      form: {
        type: 1,
        email: null,
        title: null,
        content: null,
        imgUrl: null,
      },
      img: process.env.VUE_APP_IMG_API,
      rules: {
        type: [{ required: true, message: "", trigger: "blur" }],
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern:
              /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/,
            message: "",
          },
        ],
        title: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  methods: {
    screenshot() {
      this.$toast({ type: "error", message: "!" });
    },
    closeDialog() {
      this.form.type = 1;
      this.form.email = null;
      this.form.title = null;
      this.form.content = null;
      this.$store.commit("setDialogFormVisible");
    },
    submit() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          addFeedback(this.form)
            .then((res) => {
              this.$toast({ type: "success", message: "" });
              this.closeDialog();
            })
            .catch((err) => {
              this.$toast({ type: "error", message: "" });
            });
        } else {
          this.$toast({ type: "error", message: "" });
        }
      });
    },
  },
  computed: {
    blogInfo() {
      return this.$store.state.blogInfo;
    },
  },
};
</script>
